
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 1200px;
  margin: 42px auto;
}
@media all and (max-width: 1024px) {
#app {
    max-width: 100%;
    margin: 21px;
}
#app p {
      font-size: 1.25em;
}
}



